  width: 95%;
  max-width: 280px;
  height: 885px;
  opacity: 1;
  background-color: tomato;
  border-radius: 25px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
